import {mapState,mapMutations} from 'vuex'
import uniBadge from "@/components/uni-badge/uni-badge.vue"
export default {
	data() {
		return {
			sessionId:'',
			myData:[]
		}
	},
	onLoad(option) {
		this.init()
	},
	computed: mapState(['hasLogin','uerInfo']),
	methods: {
		...mapMutations(['login']),
		init(){
			uni.getStorage({
				key: 'userInfo',
				success:(res) => {
					this.login(res.data);
					this.sessionId = res.data.sessionId
					uni.request({
						url:'https://www.nssnail.com/wx/getGoods',
						data:{
							sessionId:this.sessionId
						},
						success: (res) => {
							if(res.data.stat == 1){
								this.myData = res.data.data
								this.myData.forEach((item)=>{
									if(item.stat == '1'){
										item.stat = '上架'
									}else{
										item.stat = '下架'
									}
								})
							}else if(res.data.stat == 3 || res.data.stat == 4){
								uni.showModal({
								    title: '提示',
								    content: '登录过期，请重新登录',
								    success: function (res) {
								        if (res.confirm) {
								            uni.navigateTo({
								            	url:'../myInit/myInit'
								            })
								        }
								    }
								});
							}
						}
					})
				} 
			})
		},
		
		// 跳转至详情页
		gotoGoodsDetail(item){
			var url = 'http://image.nssnail.com/'+item.fileList[0].relativePath
			var id = item.id
			uni.navigateTo({
				url:'../goodsDetail/goodsDetail?id='+id+'&sessionId='+this.sessionId+'&url='+url
			})
		}
	},
	components:{
		uniBadge
	}
}
